<template>
  <div class="termos-condicoes-wrapper">
    <h1>Termos e Condições</h1>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  metaInfo() {
    return {
      title:
        "Termos e Condições - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content:
            "Termos e Condições - Ana Carolina Pereira - Clínica de Psicologia"
        },
        {
          property: "og:description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquoise: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.termos-condicoes-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  h1 {
    margin-top: pxToEm(100);
    text-align: center;
    color: $turquoise;
  }
}
</style>




